@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    background-color: v.$verde;

    &.fijo{
         @include m.tablet {
            left: 0;
            top: 0;
            position: fixed;
        width: 100%;
        box-shadow: .1rem .2rem .3rem #666;
         }
        
    }

    h1{
        color: v.$blanco;
        text-align: center;
        @include m.tablet {
            margin: 0;
        }
        
    }
    .contenido-header{
        padding: 2rem;
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}

.nevegacion-principal{
    @include m.tablet {
        display:flex;
        gap: 2rem;
    }
        a{
            display: block;
            text-align: center;
            margin-bottom: 1rem;
            @include m.tablet {
                margin:0;
            }
            color: v.$blanco;
            font-size: 2.4rem;
            &:hover{
                color: v.$amarillo;
            }
        }
    }
    

